import customData from 'assets/customData/customerReportWeek.json'

var res = customData, customDay = []       //
var male = [], female = [], customerAll = []
var time = ['','','','','','','']
for (let i in res) {
  male[i] = res[i].malesage1+res[i].malesage2+res[i].malesage3+res[i].malesage4+res[i].malesage5
  female[i] = res[i].femalesage1+res[i].femalesage2+res[i].femalesage3+res[i].femalesage4+res[i].femalesage5
  customerAll[i] = male[i]+female[i]
  customDay.push({
    times: time[i],
    customersAll: customerAll[i],
    males: male[i],
    females: female[i]
  })
}

export default function drawCustomerReport() {
  return {
    title: { 
      left: 'center',
      text: '' 
    },
    tooltip : {
      trigger: 'axis',
      axisPointer : {            // 
        type : 'shadow'        // 'line' | 'shadow'
      }
    },
    legend: {
      orient : 'vertical',
      x : 'right',
      data: ['','']
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      containLabel: true
    },
    xAxis : [
      {
        type : 'category',
        data : time
      }
    ],
    yAxis : [
      {
        type : 'value'
      }
    ],
    series : [  
      {
        name:'',
        type:'bar',
        stack: '',
        itemStyle:{
          normal:{color:'#03a9f4'}
        },
        data: male
      },
      {
        name:'',
        type:'bar',
        stack: '',
        itemStyle:{
          normal:{color:'#e57373'}
        },
        data: female
      },
    ]
  }
}